<template>
  <div class="container">
    <h1>Notes</h1>

    <table class="table my-5">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Name</th>
          <th scope="col">Description</th>
          <th scope="col">Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notes" :key="index">
          <th scope="row">{{item._id}}</th>
          <td>{{item.name}}</td>
          <td>{{item.description}}</td>
          <td>
            <!-- <b-button @click="alert()">Action</b-button> -->
            <b-button @click="deleteNote(item._id)" class="btn-danger btn-sm mx-2">Delete</b-button>
            <b-button @click="editNote(item._id)" class="btn-warning btn-sm">Update</b-button>
          </td>
        </tr>
      </tbody>
    </table>

    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="message.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{message.text}}</b-alert>

    <form @submit.prevent="updateNote(updtNote)" v-if="update">
      <h3>Update note</h3>
      <input type="text" class="form-control my-2" placeholder="Name" v-model="updtNote.name" />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Description"
        v-model="updtNote.description"
      />
      <b-button class="btn-warning my-2 mx-2" type="submit">Update</b-button>
      <b-button class="btn-secondary my-2" type="submit" @click="update = false">Cancel</b-button>
    </form>

    <form @submit.prevent="addNote()" v-if="!update">
      <h3>Add note</h3>
      <input type="text" class="form-control my-2" placeholder="Name" v-model="note.name" />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Description"
        v-model="note.description"
      />
      <b-button class="btn-success btn-block my-2" type="submit">Add</b-button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      notes: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      message: { color: "", text: "" },
      note: { name: "", description: "" },
      update: false,
      updtNote: {}
    };
  },
  created() {
    this.listNotes();
  },
  methods: {
    alert() {
      this.message.color = "danger";
      this.message.text = "Testing alert";
      this.showAlert();
    },
    listNotes() {
      this.axios
        .get("/note")
        .then(res => {
          console.log(res);
          this.notes = res.data;
        })
        .catch(e => {
          console.log(e);
        });
    },
    addNote() {
      //console.log(this.note);
      this.axios
        .post("/new-note", this.note)
        .then(res => {
          this.notes.push(res.data);
          this.note.name = "";
          this.note.description = "";
          this.message.color = "success";
          this.message.text = "Note added";
          this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
          if (e.response.data.error.errors.name.message) {
            this.message.text = e.response.data.error.errors.name.message;
          } else {
            this.message = "Request denied";
          }
          this.message.color = "danger";
          this.showAlert();
        });
    },
    deleteNote(id) {
      console.log(id);
      this.axios
        .delete(`/note/${id}`)
        .then(res => {
          const index = this.notes.findIndex(item => item._id === res.data._id);
          this.notes.splice(index, 1);
          this.message.color = "success";
          this.message.text = "Note deleted";
          this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    editNote(id) {
      this.update = true;
      console.log(id);
      this.axios
        .get(`/note/${id}`)
        .then(res => {
          this.updtNote = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    updateNote(item) {
      this.axios
        .put(`/note/${item._id}`, item)
        .then(res => {
          const index = this.notes.findIndex(n => n._id === res.data._id);
          this.notes[index].name = res.data.name;
          this.notes[index].description = res.data.description;
          this.message.color = "success";
          this.message.text = "Note updated";
          this.showAlert();
          this.update = false;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  }
};
</script>